











import Quagga from "quagga";

import { Vue, Component, Watch, Prop } from "vue-property-decorator";

@Component
export default class BarcodeScanner extends Vue {
	result: string = "";
	resultResetTimer: any = 0;

	@Prop({ default: () => {} }) onDetected: Function | undefined;

	start() {
		Quagga.onDetected(this.onDet);
		Quagga.onProcessed(this.onProcessed);

		let config = {
			inputStream: {
				name: "Live",
				type: "LiveStream",
				target: this.$refs.screen,
				constraints: {
					width: 1024,
					height: 1024,
					facingMode: "environment",
				},
			},
			decoder: {
				readers: ["code_128_reader", "ean_reader", "ean_8_reader", "code_39_reader", "code_39_vin_reader", "code_93_reader"],
			},
			locate: false, // 가로 인식
			numOfWorkers: navigator.hardwareConcurrency,
			frequency: 10,
		};

		Quagga.init(config, (err) => {
			if (err) {
				console.log(err);
				return;
			}
			console.log("Initialization finished. Ready to start");
			Quagga.start();
		});
	}
	stop() {
		Quagga.stop();
	}
	onDet(data) {
		let barcode = data.codeResult.code;
		if (barcode != this.result && barcode) {
			if (this.onDetected) this.onDetected(barcode);
			this.result = barcode;
			if (!this.resultResetTimer) {
				this.resultResetTimer = setTimeout(() => {
					this.result = "";
					this.resultResetTimer = 0;
				}, 1000);
			}
		}
	}
	onProcessed(result) {
		let drawingCtx = Quagga.canvas.ctx.overlay;
		let drawingCanvas = Quagga.canvas.dom.overlay;
		if (result) {
			if (result.codeResult && result.codeResult.code) {
				drawingCtx.clearRect(0, 0, parseInt(drawingCanvas.getAttribute("width")), parseInt(drawingCanvas.getAttribute("height")));
				Quagga.ImageDebug.drawPath(result.line, { x: "x", y: "y" }, drawingCtx, { color: "red", lineWidth: 3 });
			}
		}
	}
}
