




















import { Vue, Component, Prop, Watch } from "vue-property-decorator";

@Component
export default class CreateStock extends Vue {
	name: string = "";
	// @Prop({ default: () => {} }) onSubmit!: Function;
	submit() {
		this.$emit("submit", {
			name: this.name,
		});
	}
	close() {
		this.$emit("close", false);
	}
}
