






import { Vue, Component } from "vue-property-decorator";
@Component
export default class App extends Vue {
  deferredPrompt: any;

  async created() {
    console.log("created");
    window.addEventListener("beforeinstallprompt", (e: any) => {
      e.preventDefault();
      this.deferredPrompt = e;
      e.prompt();
    });
  }
}
